.intro {
	background-image: url('../img/bg-intro.jpg');
	background-size: cover;
	height: 560px;
	position: relative;
	overflow: hidden;
	@include r(1199) {
		height: 450px; }
	@include r($tablet) {
		height: 415px; }
	&__text {
		color: $white;
		position: absolute;
		bottom: 70px;
		@include r(1679) {
			margin-left: -160px; }
		@include r(1439) {
			margin-left: -220px; }
		@include r(1279) {
			margin-left: -260px; }
		@include r($tablet) {
			margin-left: 0;
			position: static;
			margin-top: 100px; }
		p {
			font-size: 26px; }
		h2 {
			font-size: 55.5px;
			line-height: 1.16;
			position: relative;
			@include r(1199) {
				font-size: 40px; }
			@include r($tablet) {
				font-size: 44.5px;
				margin-bottom: 50px; }
			&:before {
				content: '';
				@include s('arrow');
				display: block;
				position: absolute;
				right: -210px;
				top: -150px;
				@include r($tablet) {
					content: none; } } }
		.btn {
			width: 350px;
			float: right; } }
	&__form {
		background: $white;
		padding: 45px 60px;
		margin-top: 50px;
		@include r(1679) {
			margin-top: 55px; }
		@include r(1279) {
			padding: 30px;
			margin-top: 95px; }
		@include r(1199) {
			margin-top: 22px; }
		form {
			h3 {
				font-size: 32px;
				margin-bottom: 35px;
				color: #3f3f3f;
				@include r(1679) {
					font-size: 21px; }
				@include r(1439) {
					font-size: 18px;
					br {
						display: none; } }
				@include r(1199) {
					font-size: 16px;
					margin-bottom: 10px; } }
			p {
				margin-top: 30px;
				font-weight: 300;
				@extend .roboto;
				font-size: 18.5px;
				color: $black-three;
				@include r(1679) {
					font-size: 16px;
					br {
						display: none; } }
				@include r(1199) {
					font-size: 13px; } } } } }
