.doctors {
  padding: 100px 0;
  @include r($tablet) {
    padding: 70px 0; }
  .col-xs-12 {
    padding: 0; }
  article {
    width: 33.33%;
    @include tr;
    cursor: pointer;
    @include r($tablet) {
      width: 100%;
      display: flex;
      flex-wrap: wrap; }
    header {
      overflow: hidden;
      position: relative;
      height: 400px;
      @include r(1800) {
        height: 345px; }
      @include r(1679) {
        height: 295px; }
      @include r(1439) {
        height: 255px; }
      @include r(1279) {
        height: 235px; }
      @include r(1199) {
        height: 200px; }
      @include r($tablet) {
        height: 350px;
        width: 40%; }
      img {
        width: 100%;
        @include r($tablet) {
          height: 100%;
          width: initial;
          position: absolute;
          left: 50%;
          transform: translateX(-50%); } }
      h1 {
        position: absolute;
        bottom: 35px;
        left: 60px;
        color: $white;
        font-size: 28px;
        @include r(1279) {
          font-size: 22px; }
        @include r(1199) {
          font-size: 18px;
          bottom: 20px;
          left: 20px; }
        @include r($tablet) {
          font-size: 24px;
          bottom: 30px;
          left: 15px; } } }
    section {
      padding: 40px 65px;
      @include r(1439) {
        padding: 20px; }
      @include r($tablet) {
        width: 60%;
        padding: 38px 28px; }
      p {
        font-size: 20px;
        color: #3f3f3f;
        line-height: 1.35;
        @extend .roboto;
        margin-bottom: 35px;
        @include r(1279) {
          font-size: 16px; }
        @include r($tablet) {
          margin-bottom: 50px; }
        strong {
          @extend .ubuntu;
          font-weight: 700; } }
      div {
        margin-bottom: 40px;
        a {
          color: #3f3f3f;
          display: flex;
          align-items: center;
          text-decoration: none;
          font-size: 20px;
          text-transform: uppercase;
          i {
            font-size: 34px;
            margin-right: 15px; } } }
      .btn {
        width: 100%; } }
    &:hover {
      box-shadow: 0px 3px 87px 0 #cfd7e4; } } }

